@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Arial';
  scroll-behavior: smooth;
  /* display: none; */
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gridArea {
  grid-area: 1/1/2/2;
}

.element-class {
  position: relative;
}

.element-class::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url('https://thesagardevendra.github.io/spotifyclone/arjithsingh.jpg');
  filter: blur(5px);
  /* Adjust the pixel value for desired blur effect */
  z-index: -1;
  /* Place the pseudo-element below the content */
}

.rhap_main-controls {
  align-items: end !important;
}

.widthChild {
  width: 100%;
}

.rhap_rewind-button,
.rhap_forward-button,
.rhap_volume-controls,
.rhap_progress-section {
  display: none !important;
}

@media screen and (min-width: 768px) {
  body{
    display: block;
  }
  .widthChild {
    width: calc(100vw - 272px);
  }

  .rhap_rewind-button,
  .rhap_forward-button {
    display: block !important;
  }

  .rhap_container {
    background-color: rgb(9 9 11) !important;
    box-shadow: none !important;
    width: 500px !important;
  }

  .rhap_volume-controls,.rhap_progress-section {
    display: flex !important;
  }
}

.rhap_container {
  background-color: rgb(9 9 11) !important;
  box-shadow: none !important;
  /* width: 500px !important; */
}

.rhap_main {
  flex-direction: column-reverse !important;
}

.rhap_time {
  color: rgb(190, 185, 185) !important;
  font-size: 14px !important;
}

.rhap_download-progress {
  background-color: rgb(62, 61, 61) !important;
  border-radius: 5px !important;
}

.rhap_progress-filled {
  background-color: rgb(34 197 94) !important;
}

.rhap_progress-indicator {

  background-color: white !important;
  height: 13px !important;
  width: 13px !important;
  top: -4px !important
}

.rhap_volume-indicator {
  top: -3.5px !important;
  background-color: white !important;
  height: 10px !important;
  width: 10px !important;
  opacity: 100 !important;
}

.rhap_repeat-button {
  display: none !important;
}

.rhap_button-clear {
  color: white !important;
}